import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute} from '@angular/router';
import { HttpClient,HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-particular',
  templateUrl: './particular.component.html',
  styleUrls: ['./particular.component.css']
})
export class ParticularComponent implements OnInit {

  param1: string;
  receiveremail : string;
  bbookname : string;

  constructor(private router: ActivatedRoute,private route: Router,private http: HttpClient) {
    this.param1 = this.router.snapshot.params.id;
    //alert(this.param1);
   }

  ngOnInit() {
    this.getparticularbooks();
  }

  Particular: Object;

  getparticularbooks(){

   var p = {
      id : this.param1
   }

    const headers = new HttpHeaders()
              .set('Authorization', 'my-auth-token')
              .set('Content-Type', 'application/json');
    
        this.http.post('http://localhost:2678/particularbook',JSON.stringify(p), {
          headers: headers
        })
        .subscribe(data => {
         // alert(data);
          console.log(JSON.stringify(data));
          this.Particular = data;
         //console.log(data[0].bookname);
         this.receiveremail = data[0].selleremail;
         this.bbookname = data[0].bookname;
        });
  
  } 


  addtowishlist(id,name,bookname,authorname,price,condition,imageurl){
    // event.preventDefault();
    // const target = event.target;
    // const password = target.querySelector('#password').value;

    var p = {
      Bookid : id,
      Name:name,
      Bookname:bookname,
      Authorname:authorname,
      Price:price,
      Condition:condition,
      Imageurl:imageurl
      //Password : password
   }

   console.log(p);

    const headers = new HttpHeaders()
              .set('Authorization', 'my-auth-token')
              .set('Content-Type', 'application/json');
    
        this.http.post('http://localhost:2678/wishlist',JSON.stringify(p), {
          headers: headers
        })
        .subscribe(data => {
          alert(data);
          console.log(JSON.stringify(data));
          //this.Particular = data;
        });

  }



   sendmessage(event){
    event.preventDefault();
    const target = event.target;
    const message = target.querySelector('#message').value;

    var p = {
      Receiveremail:  this.receiveremail,
      Bookname: this.bbookname,
      Message: message
      //Password : password
   }

   console.log(p);

    const headers = new HttpHeaders()
              .set('Authorization', 'my-auth-token')
              .set('Content-Type', 'application/json');
    
        this.http.post('http://localhost:2678/message',JSON.stringify(p), {
          headers: headers
        })
        .subscribe(data => {
          alert(data);
          console.log(JSON.stringify(data));
          //this.Particular = data;
        });

   }



  

}
