import { Component, OnInit } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.css']
})
export class MessagesComponent implements OnInit {

  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.getAllMessages();
  }

  recievedmessages : Object;

  getAllMessages(){

    // var p = {
    //    id : this.param1
    // }
 
     const headers = new HttpHeaders()
               .set('Authorization', 'my-auth-token')
               .set('Content-Type', 'application/json');
     
         this.http.get('http://localhost:2678/getmessage', {
           headers: headers
         })
         .subscribe(data => {
          alert(data);
           console.log(JSON.stringify(data));
           this.recievedmessages = data;
         });
   
   }
   
   

   sendmessage(event,receiveremail,bbookname){
    event.preventDefault();
    const target = event.target;
    const message = target.querySelector('#message').value;

    var p = {
     Receiveremail: receiveremail,
      Bookname: bbookname,
      Message: message
      //Password : password
   }

   console.log(p);

    const headers = new HttpHeaders()
              .set('Authorization', 'my-auth-token')
              .set('Content-Type', 'application/json');
    
        this.http.post('http://localhost:2678/message',JSON.stringify(p), {
          headers: headers
        })
        .subscribe(data => {
          alert(data);
          console.log(JSON.stringify(data));
          //this.Particular = data;
        });

   }


}
