import { Component, OnInit } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css']
})
export class AddComponent implements OnInit {

  constructor(private http: HttpClient) { }

  ngOnInit() {
  }

  addproduct(event){
    event.preventDefault();
    const target = event.target;
    const username = target.querySelector('#Username').value;
    const bookname = target.querySelector('#Bookname').value;
    const authorname = target.querySelector('#Authorname').value;
    const imageurl = target.querySelector('#Imageurl').value;
    const price = target.querySelector('#Price').value;
    const condition = target.querySelector('#Condition').value;


    var p={
          Name : username,
          Bookname : bookname,
          Authorname: authorname,
          Imageurl: imageurl,
          Price : price,
          Condition: condition
      };

      //console.log(p);
      alert("refresh page");
        const headers = new HttpHeaders()
              .set('Authorization', 'my-auth-token')
              .set('Content-Type', 'application/json');
    
        this.http.post('http://localhost:2678/books', JSON.stringify(p), {
          headers: headers
        })
        .subscribe(data => {
          alert(data);
          console.log(data);
        });

  }

}
